import { useState } from "react";
import { Link, useHistory } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircleNotch } from "@fortawesome/free-solid-svg-icons";

import {
  getImageUrlFromStorage,
  postUser,
  createNativeUser,
} from "../../utils/firebase";
import logo from "../../images/logo-full.png";

import styles from "./SignUp.module.scss";

import Alert from "../Alert/Alert";

const SignUp = () => {
  let history = useHistory();
  let urlParams = new URLSearchParams(window.location.search);
  let to = urlParams.get("to");

  const [inputs, setInputs] = useState({ email: "", password: "", name: "" });
  const [isLoading, setIsLoading] = useState(false);
  const [showAlert, setShowAlert] = useState(false);
  const [alertText, setAlertText] = useState("");

  const handleInputChange = (e) => {
    setInputs({
      ...inputs,
      [e.target.name]: e.target.value,
    });
  };

  const signUpWithEmailPassword = (email, password, displayName) => {
    setIsLoading(true);
    getImageUrlFromStorage("usersProfile", "avatar-4.png", (urlData) => {
      createNativeUser(email, password)
        .then((res) => {
          postUser(displayName, urlData);
          setInputs({ email: "", password: "", name: "" });
          setIsLoading(false);
          history.push("/");
        })
        .catch((error) => {
          setIsLoading(false);
          if (error.code === "auth/invalid-email") {
            setShowAlert(true);
            setAlertText("");
          } else if (error.code === "auth/weak-password") {
            setShowAlert(true);
            setAlertText("6");
          }
        });
    });
  };

  const toggleAlert = () => {
    setShowAlert(false);
  };

  return (
    <div className={styles.background}>
      <div className={styles.container}>
        <div className={styles.left}></div>
        <div className={styles.right}>
          <div className={styles.signup}>
            <div className={styles.logo}>
              <Link to="/">
                <img src={logo} alt="logo"></img>
              </Link>
            </div>
            <h2 className={styles.title}></h2>
            <label htmlFor="email">
              <span>(10)</span>
            </label>
            <input
              type="text"
              name="name"
              value={inputs.name}
              onChange={handleInputChange}
              placeholder=""
            ></input>
            <label htmlFor="email"></label>
            <input
              type="email"
              name="email"
              value={inputs.email}
              onChange={handleInputChange}
              placeholder="email"
            ></input>
            <label htmlFor="password">
              <span>(6)</span>
            </label>
            <input
              type="password"
              name="password"
              value={inputs.password}
              onChange={handleInputChange}
              placeholder=""
            ></input>
            <button
              className={styles.fullBtn}
              onClick={() =>
                signUpWithEmailPassword(
                  inputs.email,
                  inputs.password,
                  inputs.name
                )
              }
            >
              {isLoading ? (
                <FontAwesomeIcon icon={faCircleNotch} spin />
              ) : (
                ""
              )}
            </button>
            <div>
              
              <Link to={to ? `/login?to=${to}` : "/login"}>
                <span></span>
              </Link>
            </div>
          </div>
        </div>
      </div>
      {showAlert && <Alert text={alertText} handelConfirm={toggleAlert} />}
    </div>
  );
};

export default SignUp;
